.order-modal {
  position: fixed;
  z-index: 1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0,0.4);
}  
.order-modal-content {
    text-align: left;
  background-color: #fefefe;
  margin: 10% auto; 
  padding: 20px;
  border: 1px solid #888;
  width: 60%;
  font-family: Montserrat;
}
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}  
.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
.button-div{      
  display: inline-flex;
  margin: 3%;
  text-align: center;
}
.button-div>button {
  color:black;
  width: 130px;
}